import React, { Component } from 'react';
import DatePickerDesktop from './pages/DatepickerDesktop';
import DatePickerDesktopPrice from './pages/DatepickerDesktopPrice';
import DatePickerMobile from './pages/DatepickerMobile';
import { isMobile, isBrowser } from "react-device-detect";
import {
	Route,
	Switch,
	Redirect,
	HashRouter,
	withRouter
} from "react-router-dom";
import './App.css';

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			car_availability: null,
			car_prices: null,
			datesBlocked: null
		};
	}

	componentDidMount() {
		if (isMobile) {
			document.getElementsByTagName("html")[0].classList.add('is-mobile-device');
		}

		if (isBrowser) {
			document.getElementsByTagName("html")[0].classList.add('is-desktop-device');
		}
	}

	render() {
		const { history } = this.props
	
		return (
			<div className="App" >
				<HashRouter history={history} basename='/'>
					<Switch>
						<Route history={history} path='/main' component={DatePickerDesktop} />
						<Route history={history} path='/profile' component={DatePickerDesktopPrice} />
						<Route history={history} path='/mobile' component={DatePickerMobile} />
						<Redirect from='/' to='/main' />
					</Switch>
				</HashRouter>
			</div>
		);
	}
}

export default withRouter(App);